import React, { Component } from 'react';

import Header from "./HeaderContainer";
import api from "../utils/api";
import '../styles/report.scss';
import backImg from '../images/global/btn-back@2x.png';


class ReviewContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      participation : false,
      livelihood: false,
      focus: false,
      etc: false,
      mentorComment: "",
    };
    this.goBack = this.goBack.bind(this);
    this.clickOption = this.clickOption.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
  }

  async componentDidMount() {
  //   const response = await api('get','/api/v1/community');
  //   if(!response) return;
  //   this.setState({boards: response.data.data});
  }

  onChangeHandler(e) {
    this.setState({[e.target.name]:  e.target.value})
  }

  goBack(){
    const {history} = this.props;
    history.goBack();
  }

  clickOption(e) {
    const {[e.target.id] : value} = this.state;
    this.setState({[e.target.id]: !value })
  }

  render() {
    const {participation, livelihood, focus, etc, mentorComment} = this.state;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">   </div>
          <div className={`register-btn ${participation || livelihood || focus || etc ? "on" : ""}`} onClick={this.postApi}></div>
        </div>
        <div className="report-container child-container">
          <div className="content">
            <div className="desc">
              <span>   .</span>
              <p className="sub-desc">(  )</p>
            </div>
            <div className={`review-option ${participation ? "on" : ""}`} onClick={this.clickOption} id="participation">    .</div>
            <div className={`review-option ${livelihood ? "on" : ""}`} onClick={this.clickOption} id="livelihood">   .</div>
            <div className={`review-option ${focus ? "on" : ""}`} onClick={this.clickOption} id="focus">    .</div>
            <div className={`review-option ${etc ? "on" : ""}`} onClick={this.clickOption} id="etc"> </div>
            {
              etc ? (
                <input
                  className="etc-comment"
                  type="text"
                  mentor="mentorComment"
                  onChange={this.onChangeHandler}
                  value={mentorComment}
                />) : null
            }
          </div>
        </div>
      </div>
    )
  }
}

